.home-banner {
	content: '';
	width: 100%;
	height: 442px;
	position: relative;
}

.home-banner .bg {
	background: url(/static/media/bg_3_blur.c1486126.jpg) bottom left;
	background-size: 100% auto;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: -2;
}

.home-banner .overlay {
	background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAADCAYAAABWKLW/AAAABGdBTUEAAK/INwWK6QAAABl0RVh0U29mdHdhcmUAQWRvYmUgSW1hZ2VSZWFkeXHJZTwAAAAdSURBVHjaYmBgYPD9//8/AwgzgggYYGJAAgABBgAL7wlIbK4OiwAAAABJRU5ErkJggg==);
	position: absolute;
