.wrapper {
    width: 100%;
    height: 100vh;
    position: relative;
}
.wrapper .bg {
    background: #348cb2 url(/static/media/bg_1.f7de43c6.jpg) top left;
    background-size: 100% 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -10;
}
@media (max-width: 992px) {
    .wrapper .bg {
        background-size: auto 100%;
    }
}
.wrapper .overlay {
    background: #3e64ff;
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -9;
    opacity: .3;
}
.wrapper .main {
    margin: auto 0;
}
.wrapper .main h1 {
    font-size: 3.5em;
    font-weight: 900;
    letter-spacing: -0.035em;
    line-height: 1em;
    color: #ffffff;
}
.wrapper .main h1 span {
    color: #9FF569;
}
.wrapper .main p {
    font-size: 1.25em;
    margin: 0.75em 0 0.25em 0;
    opacity: 0.75;
    color: #ffffff;
}
.wrapper .main nav {
    margin: 1.5em 0 0 0;
}
.wrapper .main nav ul {
    margin: 0;
    padding: 0;
}
.wrapper .main nav li {
    display: inline-block;
    height: 5.35em;
    line-height: 5.885em;
    width: 5.35em;
}
.wrapper .main nav a {
    border: 0;
    display: inline-block;
    text-decoration: none;
    color: #fff;
}
.wrapper .main nav a:before {
    border-radius: 100%;
    border: solid 1px #fff;
    display: block;
    font-size: 1.75em;
    height: 2.5em;
    line-height: 2.5em;
    position: relative;
    text-align: center;
    top: 0;
    width: 2.5em;
}
.wrapper .main nav a:hover {
    font-size: 1.0em;
}
.wrapper .main nav a:hover:before {
    background-color: rgba(255, 255, 255, 0.175);
    color: #fff;
}
.wrapper .main nav a:active {
    font-size: 0.95em;
    background: none;
}
.wrapper .main nav a:active:before {
    background-color: rgba(255, 255, 255, 0.35);
    color: #fff;
}
.wrapper .main nav a span {
    display: none;
}
.block-20 {
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    position: relative;
    display: block;
    width: 100%;
    height: 300px;
}
.blog-entry {
    position: relative;
    height: 100%;
}
@media (min-width: 768px) {
    .blog-entry {
        margin-bottom: 30px;
    }
}
@media (max-width: 767.98px) {
    .blog-entry {
        margin-bottom: 30px;
    }
}
.blog-entry .text {
    width: 100%;
    margin: 0 auto;
}
.blog-entry .text .heading {
    font-size: 20px;
    margin-bottom: 16px;
    font-weight: 500;
}
.blog-entry .text .heading a {
    color: #000000;
    text-decoration: none;
}
.blog-entry .text .heading a:hover, .blog-entry .text .heading a:focus, .blog-entry .text .heading a:active {
    color: #3e64ff;
}
.blog-entry .meta {
    display: inline-block;
    margin-right: 5px;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: 600;
}
.blog-entry .meta span {
    color: #3e64ff;
}
.blog-entry .meta a {
    color: #3e64ff;
}
.blog-entry .meta a:hover {
    color: #3e64ff;
}
.contact-box {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 4px;
}
.contact-box .icon {
    width: 100px;
    height: 100px;
    background: #3e64ff;
    margin: 0 auto;
    margin-bottom: 10px;
    border-radius: 50%;
}
.contact-box .icon span {
    color: #fff;
}
.contact-box h3 {
    font-weight: 600;
    color: #000;
}
.card.service {
    height: 100%;
}
.card.service h5.card-title {
    position: relative;
    margin-bottom: 30px;
}
.card.service h5.card-title:after {
    position: absolute;
    bottom: -15px;
    left: 0;
    right: 0;
    content: '';
    width: 30px;
    height: 1px;
    background: #3364FF;
    margin: 0 auto;
}
.card.service .icon {
    display: block;
    margin-bottom: 30px;
}
.card.service .icon i {
    line-height: 1.0;
    color: #3e64ff;
}
.card.service .card-text {
    color: #777;
}
.card.counter-wrap .card-body h3.number {
    color: #3e64ff;
}
.card.counter-wrap .card-body span {
    color: #000000;
}


